html {
	margin: 0;
	background-color: black;
	color: white;
}

.flex {
	display: flex;
}

.flex-1 {
	flex: 1;
}

.flex-direction-row {
	flex-direction: row;
}

.container {
	display: grid;
	grid-template-columns: 1fr 500px 1fr;
}

.title {
	font-family: 'Playfair Display', serif;
	font-size: 3.2em;
}

.description {
	font-family: 'Open Sans', sans-serif;
	font-size: 1em;
}